import React, { useState } from "react";
import { Box, Tabs, Tab, Heading, Button, TextInput } from "grommet";
import { Facebook, Tools } from "grommet-icons";
import Parse, { signUp, resetPassword, logIn } from "../library/parse";
import { navigate } from "gatsby";

const Component = ({ onClose }) => {
  const [model, setModel] = useState({});
  const [message, setMessage] = useState("");
  const [mode, setMode] = useState("Login");
  return (
    <Box>
      <Heading level={3}> Please login to Bid</Heading>
      Email:
      <TextInput
        value={model["email"]}
        onChange={(event) => {
          setModel({
            ...model,
            username: event.target.value,
            email: event.target.value,
          });
        }}
        placeholder="email"
      />
      Password:
      <TextInput
        value={model["email"]}
        onChange={(event) => {
          setModel({
            ...model,
            password: event.target.value,
          });
        }}
        placeholder="password"
      />
      <Button
        label="Login"
        primary
        onClick={() => {
          if (model.email) {
            logIn(model.email, model.password)
              .then((e) => navigate("/"))
              .catch((e) =>
                setMessage(
                  "Please login with the correct username and password"
                )
              );
          }
        }}
      />
      {message}
    </Box>
  );
};
export default Component;
